import React from 'react';
import { string, shape } from 'react-proptypes';

import Icon from '../../../components/Icon';

import './TitleBlock.scss';

const TitleBlock = ({ params }) => (
    <div className="title-block__container">

        <div className="title-block__content">
            <h1>
                {params.title}
            </h1>

            <p  className="subline2"
                dangerouslySetInnerHTML={{ __html: params.description }} />
        </div>
    </div>
);

TitleBlock.propTypes = {
    params: shape({
       title: string,
       description: string,
       inputPlaceholder: string,
       buttonText: string
    })
};

export default TitleBlock;



