import React from 'react';
import Link from 'gatsby-link';

import { TitleBlock } from './TitleBlock';
import { SkillsList } from './SkillsList';
import { ExtraBlock } from './ExtraBlock';
import './index.scss';

export default ({ data }) => {
    const page = data.markdownRemark;
    const { title, titleBlock, skills, extraBlock } = page.frontmatter;

    return (
        <div className="skills__container">
            <TitleBlock params={titleBlock} />

            <SkillsList params={skills} />

            <ExtraBlock params={extraBlock} />
        </div>
    );
};

export const query = graphql`
    query SkillsPageQuery {
        markdownRemark(fields: { slug: { eq: "/skills/" } }) {
            html
            frontmatter {
                title
                titleBlock {
                    title
                    description
                }
                skills {
                    title
                    description
                    image
                }
                extraBlock {
                    title
                    description
                }
            }
        }
    }
`;



