import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';

import './SkillsList.scss';

const Skill = ({ title, description, action, image, linkContent, linkUrl }, index) => (
    <div className="skill__container">
        <div className="skill__content"
            style={{order: index % 2 === 0 ? 0 : 1}}>
            <h2>
                {title}
            </h2>

            <p className="subline2">
                {description}
            </p>

            {
                linkContent &&
                (<a href={linkUrl} className="button-text-large">
                    {linkContent}
                </a>)
            }
        </div>

        <div className="skill__image"
            style={{order: index % 2 === 0 ? 1 : 0}}>
            {/* <div style={{'background-image': `url('./${image}')`}} /> */}
            <div />
        </div>
    </div>
);

const SkillsList = ({ params }) => {
    const count = params.length;

    const pairDivs = [];

    params.forEach((item, index) => {
        if (index % 2 === 1) {
            const pairDiv = (
                <div className="pair__container"
                    key={index}>
                    {Skill(params[index - 1], index - 1)}
                    {Skill(item, index)}
                </div>
            );

            pairDivs.push(pairDiv);
        }
    });

    return (
        <div className="skills-list__container">
            {pairDivs}
        </div>
    );
};

SkillsList.propTypes = {
    items: arrayOf(shape({
       title: string,
       description: string,
       action: string,
       actionUrl: string,
       image: string 
    }))
};

SkillsList.defaultProps = {
    items: []
};

export default SkillsList;



